import { Component, OnInit, ViewChild } from '@angular/core';
import { UnscheduledMelts } from './unscheduled-melts';
import { Http } from '@angular/http';
import { ProductionMonitoringService } from '../production-monitoring.service';
import {  MatDialog } from '@angular/material';
import { ScheduleSplittingComponent } from '../unscheduled-melts/schedule-splitting/schedule-splitting.component';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-unscheduled-melts',
  templateUrl: './unscheduled-melts.component.html',
  styleUrls: ['./unscheduled-melts.component.scss']
})
export class UnscheduledMeltsComponent implements OnInit {
  public unscheduledMelts: UnscheduledMelts[];
  public unscheduledMelt: UnscheduledMelts;
  private notifier: NotifierService;
  constructor(public http: Http, public dialog: MatDialog,
    public productionService: ProductionMonitoringService, notifier: NotifierService) {
    this.notifier = notifier;
  }



  ngOnInit() {
    this.unscheduledMelt = new UnscheduledMelts();
    this.getUnscheduledMelts();
  }

  getUnscheduledMelts() {
    this.productionService.getUnscheduledMelts().subscribe((data) => {
      this.unscheduledMelts = data;
    }, (error) => {
      console.log(error);
    });
  }

  schedule(unscheduledMelt: UnscheduledMelts) {
    const dialogRef = this.dialog.open(ScheduleSplittingComponent,
      { data: { unscheduledMelt: unscheduledMelt } }
    );
    dialogRef.afterClosed().subscribe(result => {
     this.getUnscheduledMelts();
    });
  }
}
