import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ProductionMonitoringService } from '../../production-monitoring.service';
import { OngoingMelts } from '../../ongoing-melts/ongoing-melts';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-melt-weight-updation',
  templateUrl: './melt-weight-updation.component.html',
  styleUrls: ['./melt-weight-updation.component.scss']
})
export class MeltWeightUpdationComponent implements OnInit {
  public onGoing: OngoingMelts[];
  private notifier: NotifierService;
  constructor(public dialogRef: MatDialogRef<MeltWeightUpdationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, notifier: NotifierService,
    public productionService: ProductionMonitoringService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.getOnGoingMelt(this.data.id);
  }
  getOnGoingMelt(id) {
    this.productionService.getOngoingMelt(id).subscribe(data => {
      this.onGoing = data;
      let i = 0;
      this.onGoing.forEach(element => {
        this.onGoing[i].quantity = element.meltWeight;
        i++;
      })
    }, error => {
      console.log(error);
    })

  }

  updateMeltWeight() {
    let i = 0;
    this.onGoing.forEach(element1 => {
      this.onGoing[i].meltWeight = element1.quantity;
      this.onGoing[i].modifiedBy = localStorage.getItem('user');
      this.productionService.updateCurrentOngoingMelt(this.onGoing[i]).subscribe(data => {
        i++;
        if (this.onGoing.length == i) {
          this.dialogRef.close();
          this.notifier.notify('success', 'Updated Successfully');
        }
      }, error => {
        console.log(error);
      }
      )

    })
  }


}



