import { Component, OnInit } from '@angular/core';
import { Melting } from './melting';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { MatDialog} from '@angular/material';
import { CheckMouldingComponent } from './check-moulding/check-moulding.component';
import { Melt } from '../melt';
import { Moulding } from '../moulding/moulding';
import { NotifierService } from 'angular-notifier';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { TimeValidationComponent} from '../time-validation/time-validation.component';
@Component({
  selector: 'app-melting',
  templateUrl: './melting.component.html',
  styleUrls: ['./melting.component.scss'],
  providers: [DatePipe],
})

export class MeltingComponent implements OnInit {
  public labourMaster: LabourMaster;
  public melting: Melting;
  public meltings: Melting[];
  public moulding: Moulding;
  public startTime;
  public stopTime;
  public melt: Melt;
  public otHour = false;
  private notifier: NotifierService;
  constructor(private dialog: MatDialog, public productionMasterService: ProductionMasterService,
    public productionService: ProductionMonitoringService, notifier: NotifierService, public router: Router,
    public datePipe: DatePipe) {
    this.notifier = notifier;
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
  }

  ngOnInit() {
    this.melt = new Melt();
    this.melting = new Melting();
    this.getCurrentMeltNo();
    this.getLabourMaster();
    this.moulding = new Moulding();
  }

  getCurrentMeltNo() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getCurrentMelting();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getCurrentMelting() {
    this.productionService.getCurrentMelting(this.melt.meltNo).subscribe((data) => {
      this.meltings = data;
    }, (error) => {
      console.log('List cannot get');
    });
  }


  getLabourMaster() {
    let type = "Melting";
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(type).subscribe(data => {
      this.labourMaster = data;
        if (  this.labourMaster.otHours) {
          this.otHour = true;
        }
    }, error => {
      console.log(error);
    })
  }

  meltStartTime() {
    let message;
    this.router.navigated = false;
    this.productionService.checkCurrentMoulding(this.melt.meltNo).subscribe((data) => {
      message = data.message;
      this.moulding = data.res1;
      if (message === 'Not-Completed') {
        const dialogRef = this.dialog.open(CheckMouldingComponent, {
          data: { moulding: this.moulding }
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      } else if (message === 'Completed') {
        this.startTime = new Date();
        this.melting.startTime = this.startTime;
        this.melting.meltNo = this.melt.meltNo;
        this.melting.createdBy = localStorage.getItem('user');
        this.productionService.saveMelting(this.melting).subscribe((data1) => {
          this.melting = data1;
          this.notifier.notify('success', 'Melting Start Successfully');
        }, (error) => {
          console.log(error);
          this.notifier.notify('error', 'Not Start Melting');
        });
      }
    }, (error) => {
      console.log(error);
    });
  }

  meltStopTime() {
    this.stopTime = new Date();
    this.melting.endTime = this.stopTime;
    this.melting.modifiedBy = localStorage.getItem('user');
    this.productionService.updateMelting(this.melting).subscribe((data) => {
      this.notifier.notify('success', 'Melting End Successfully');
      this.productionService.updateStatusMelt(this.melt.meltNo).subscribe((data1) => {
        this.notifier.notify('success', 'Melt Successfully Completed');
      }, (error) => {
        console.log(error);
        this.notifier.notify('error', 'Not Completed Melt');
      });
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not End Melting');
    });
  }

  SaveMeltingDetails() {
    if (this.melting.endTime != undefined && this.melting.totalWorkingHours != undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.melting.endTime)).getTime() - (new Date(this.melting.startTime)).getTime();//in ms
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      var seconds = Math.round(timeDiff);
      var hour = Math.round(seconds/60);
      var minutes = seconds % 60;
      var total= Number(hour + '.' + minutes);
      if(this.melting.otHours == undefined){
        this.melting.otHours =0;
      }
      if(total < Number(this.melting.totalWorkingHours) + Number(this.melting.otHours)){
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }

    this.router.navigated = false;
    this.melting.modifiedBy = localStorage.getItem('user');
    this.productionService.updateMelting(this.melting).subscribe((data) => {
      this.notifier.notify('success', 'Melting Save Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Save Melting');
    });
  }
}
