import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MAT_DIALOG_SCROLL_STRATEGY } from '@angular/material';
import { ProductionMonitoringService } from '../../production-monitoring.service';
import { UnscheduledMelts } from '../unscheduled-melts';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'app-schedule-splitting',
  templateUrl: './schedule-splitting.component.html',
  styleUrls: ['./schedule-splitting.component.scss']
})
export class ScheduleSplittingComponent implements OnInit {
  public division: number;
  public unscheduledMelts: UnscheduledMelts[];
  quantity = 0;
  index: number;
  closed: boolean;
  public splits = true;
  private notifier: NotifierService;
  Option = [{ _id: '', partId: '', quantity: 0, partWeight: 0, totalWeight: 0, deliveryDate: '' }];

  constructor(public dialogRef: MatDialogRef<ScheduleSplittingComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private productionService: ProductionMonitoringService, notifier: NotifierService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.closed = false;
  }

  split() {
    this.splits = false;
    this.closed = true;
    let divi = Number(this.data.unscheduledMelt.quantity) / Number(this.division);
    divi = Math.round(divi);
    let i = 0;
    for (i; i < this.division; i++) {
      this.Option[i]._id = this.data.unscheduledMelt._id;
      this.Option[i].partId = this.data.unscheduledMelt.partId;
      this.Option[i].partWeight = this.data.unscheduledMelt.partWeight;
      if (i === this.division - 1) {
        let j = 0;
        for (j; j < this.division - 1; j++) {
          this.quantity = this.quantity + divi;
        }
        this.Option[i].quantity = Number(this.data.unscheduledMelt.quantity) - Number(this.quantity);
      } else {
        this.Option[i].quantity = divi;
      }
      this.Option[i].totalWeight = this.Option[i].quantity * this.Option[i].partWeight;
      let deliveryDate;
      deliveryDate = new Date(this.data.unscheduledMelt.deliveryDate);
      let deliveryDate1;
      deliveryDate1 = new Date(deliveryDate.getTime() + 7 * i * 24 * 60 * 60 * 1000);
      this.Option[i].deliveryDate = deliveryDate1.toISOString();
      this.Option.push({ _id: '', partId: '', quantity: 0, partWeight: 0, totalWeight: 0, deliveryDate: '' });
    }
    this.Option.pop();
  }

  calculateTotalWeight(index, quantity1: number) {
    let quantity = 0;
    let i;
    this.Option[index].totalWeight = this.data.unscheduledMelt.partWeight * quantity1;
    this.Option.forEach(res => {
      quantity = Number(quantity) + Number(res.quantity);
    });
    if (this.data.unscheduledMelt.quantity !== quantity) {
      if (index === 0) {
        i = index + 1;
      } else if (index > 0) {
        i = index - 1;
      }
      this.splits = true;
    } else {
      this.splits = false;
    }
  }


  schedule() {
    this.productionService.scheduleUnScheduleMelt(this.Option).subscribe(data => {
      this.dialogRef.close();
      this.notifier.notify('success', 'Scheduled Successfully');
    }, error => {
      console.log(error);
    });

  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
