import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { ProductionMonitoringComponent } from './production-monitoring.component';
import { ProductionMonitoringRoutingModule } from './production-monitoring-routing.module';
import { CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { CompletedMeltsComponent } from './completed-melts/completed-melts.component';
import { OngoingMeltsComponent } from './ongoing-melts/ongoing-melts.component';
import { ScheduledMeltsComponent } from './scheduled-melts/scheduled-melts.component';
import { MaterialModule } from '../../app.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RawMaterialConsumptionComponent } from './raw-material-consumption/raw-material-consumption.component';
import { MouldingComponent } from './moulding/moulding.component';
import { MeltingComponent } from './melting/melting.component';
import { KnockOutComponent } from './knock-out/knock-out.component';
import { ShotBlastingComponent } from './shot-blasting/shot-blasting.component';
import { FettlingComponent } from './fettling/fettling.component';
import { PaintingComponent } from './painting/painting.component';
import { SandDisposalComponent } from './sand-disposal/sand-disposal.component';
import { SharedService } from './shared/shared.service';
import { CheckMouldingComponent } from './melting/check-moulding/check-moulding.component';
import { NotifierModule, NotifierOptions } from 'angular-notifier';
import { MeltProcessComponent } from './melt-process/melt-process.component';
import { CompleteRawMaterialComponent } from './complete-raw-material/complete-raw-material.component';
import { SandItemDisposalComponent } from './sand-disposal/sand-item-disposal/sand-item-disposal.component';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { ManualTimeEntryComponent } from './manual-time-entry/manual-time-entry.component';
import { ProgressBarComponent } from './progress-bar/progress-bar.component';
import { UnscheduledMeltsComponent } from './unscheduled-melts/unscheduled-melts.component';
import { ScheduleSplittingComponent } from './unscheduled-melts/schedule-splitting/schedule-splitting.component';
import { MasterService } from '../master/master.service';
import { ProductionMonitoringService } from './production-monitoring.service';
import { WavesModule , IconsModule, InputsModule, ButtonsModule } from 'angular-bootstrap-md';
import { SandDisposalConfirmComponent } from './knock-out/sand-disposal-confirm/sand-disposal-confirm.component';
import { FoundryModule } from '../foundry.module';
import { TimeValidationComponent } from './time-validation/time-validation.component';
import { MeltWeightUpdationComponent } from './raw-material-consumption/melt-weight-updation/melt-weight-updation.component';
import { MultipleMachineEntryComponent} from './shot-blasting/multiple-machine-entry/multiple-machine-entry.component';
import { PerfectScrollbarModule, PerfectScrollbarConfigInterface,
  PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { CoreMakingComponent } from './core-making/core-making.component';
import { CoreDetailsComponent } from './core-making/core-details/core-details.component';
  const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
    wheelPropagation: true
  };
const customNotifierOptions: NotifierOptions = {
  position: {
    horizontal: {
      position: 'middle',
      distance: 12
    },
    vertical: {
      position: 'top',
      distance: 50,
      gap: 10
    }
  },
  theme: 'material',
  behaviour: {
    autoHide: 1000,
    onClick: 'hide',
    onMouseover: 'pauseAutoHide',
    showDismissButton: true,
    stacking: 4
  },
  animations: {
    enabled: true,
    show: {
      preset: 'slide',
      speed: 300,
      easing: 'ease'
    },
    hide: {
      preset: 'fade',
      speed: 300,
      easing: 'ease',
      offset: 50
    },
    shift: {
      speed: 300,
      easing: 'ease'
    },
    overlap: 10
  }
};

@NgModule({
  imports: [
    CommonModule, ProductionMonitoringRoutingModule, MaterialModule, OwlDateTimeModule, OwlNativeDateTimeModule, WavesModule,
    HttpModule, HttpClientModule, FormsModule, ReactiveFormsModule, IconsModule, InputsModule , ButtonsModule,
     NotifierModule.withConfig(customNotifierOptions), FoundryModule, PerfectScrollbarModule
  ],
  declarations:
    [ProductionMonitoringComponent, CompletedMeltsComponent, OngoingMeltsComponent,
      ScheduledMeltsComponent,
      RawMaterialConsumptionComponent,
      MouldingComponent,
      ManualTimeEntryComponent,
      MeltingComponent,
      KnockOutComponent,
      ShotBlastingComponent,
      FettlingComponent,
      PaintingComponent,
      SandDisposalComponent,
      CheckMouldingComponent,
      MeltProcessComponent,
      ManualTimeEntryComponent,
      CompleteRawMaterialComponent,
      SandItemDisposalComponent,
      ProgressBarComponent,
      UnscheduledMeltsComponent,
      ScheduleSplittingComponent,
      SandDisposalConfirmComponent,
      TimeValidationComponent,
      MeltWeightUpdationComponent,
      MultipleMachineEntryComponent,
      CoreMakingComponent,
      CoreDetailsComponent
    ],
  entryComponents: [CheckMouldingComponent, SandDisposalConfirmComponent, ManualTimeEntryComponent, CoreDetailsComponent,
    SandDisposalComponent, SandItemDisposalComponent, ScheduleSplittingComponent, TimeValidationComponent,
    MeltWeightUpdationComponent, MultipleMachineEntryComponent],
  providers: [SharedService, DatePipe, MasterService, ProductionMonitoringService,
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    }
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]

})
export class ProductionMonitoringModule { }




