import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { NotifierService } from 'angular-notifier';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { KnockOut } from '../knock-out/knock-out';
import { MatFormField, MatFormFieldControl } from '@angular/material';
import { ShotBlasting } from '../shot-blasting/shot-blasting';
import { Moulding } from '../moulding/moulding';
import { Melting } from '../melting/melting';
import { Fettling } from '../fettling/fettling';
import { Painting } from '../painting/painting';
import { Melt } from '../melt';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-manual-time-entry',
  templateUrl: './manual-time-entry.component.html',
  styleUrls: ['./manual-time-entry.component.scss'],
  providers: [DatePipe],
})
export class ManualTimeEntryComponent implements OnInit {
  public melts: Melt[];
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  public meltId: String;
  public meltNo: String;
  private notifier: NotifierService;
  public moulding: Moulding;
  public mouldings: Moulding[];
  public melting: Melting;
  public knockout: KnockOut;
  public shotBlasting: ShotBlasting;
  public fettling: Fettling;
  public painting: Painting;
  public startTime: String;

  constructor(public dialogRef: MatDialogRef<ManualTimeEntryComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, notifier: NotifierService,
    public productionService: ProductionMonitoringService, public datePipe: DatePipe) {
    this.notifier = notifier;
  }
  ngOnInit() {
    this.melt = new Melt();
    this.moulding = new Moulding();
    this.melting = new Melting();
    this.knockout = new KnockOut();
    this.shotBlasting = new ShotBlasting();
    this.fettling = new Fettling();
    this.painting = new Painting();
    this.getCurrentMelt();
  }
  getCurrentMelt() {
    this.productionService.getRecentlyCompletedMelts().subscribe((data) => {
      this.melts = data;
      data.forEach(element => {
        this.melt._id = element._id;
        this.melt.meltStartDate = element.meltStartDate;
        this.melt.meltEndDate = element.meltEndDate;
      });
    });
  }
  getChosenMelt(meltNo: String) {
    this.melts.forEach(element1 => {
      if (meltNo === element1.meltNo) {
        this.meltId = element1._id;
      }
    });
    this.getOngoingMelt(this.meltId);
  }
  getOngoingMelt(meltId: String) {
    this.productionService.getOngoingMelt(meltId).subscribe(data1 => {
      this.ongoingMelts = data1;
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        this.moulding.batchId = this.ongoingMelts[0].batchId;
        this.moulding.partId = this.ongoingMelts[0].partId;
        this.knockout.batchId = this.ongoingMelts[0].batchId.toString();
        this.knockout.partId = this.ongoingMelts[0].partId;
        this.shotBlasting.batchId = this.ongoingMelts[0].batchId;
        this.shotBlasting.partId = this.ongoingMelts[0].partId;
        this.fettling.batchId = this.ongoingMelts[0].batchId;
        this.fettling.partId = this.ongoingMelts[0].partId;
        this.painting.batchId = this.ongoingMelts[0].batchId;
        this.painting.partId = this.ongoingMelts[0].partId;
         }
        this.getMoulding(this.moulding.partId);
        this.getCurrentMelting(this.melt.meltNo);
        this.getCurrentKnockOut(this.knockout.partId);
        this.getShotBlasting(this.shotBlasting.partId);
        this.getFettling(this.fettling.partId);
        this.getPainting(this.painting.partId);

    },
      err => {
        console.log(err);
      });
  }
  getMoulding(partId: String) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentMoulding(batchId).subscribe((data) => {
      this.mouldings = data;
      if (data.length >= 1) {
        this.mouldings.forEach(res => {
          this.moulding = res;
          this.moulding._id = res._id;
          this.moulding.batchId = res.batchId;
          this.moulding.partId = res.partId;
          this.moulding.startDate = this.datePipe.transform(res.startDate, 'd/MM/yy, hh:mm a');
          this.moulding.endDate = this.datePipe.transform(res.endDate, 'd/MM/yy, hh:mm a');
          this.moulding.meltNo = res.meltNo;
        });
      } else {
        this.ongoingMelts.forEach(res => {
          if (partId === res.partId) {
            this.moulding = new Moulding();
            this.moulding.partId = res.partId;
            this.moulding.batchId = res.batchId;
            this.moulding.noOfPieces = res.quantity;
          }
        });
      }
    }, (error) => {
      console.log(error);
    });
  }
  getCurrentMelting(meltNo: String) {
    this.productionService.getCurrentMelting(meltNo).subscribe((data) => {
      if (data) {
        this.melting = data;
        this.melting.startTime = this.datePipe.transform(this.melting.startTime, 'd/MM/yy, hh:mm a');
        this.melting.endTime = this.datePipe.transform(this.melting.endTime, 'd/MM/yy, hh:mm a');
      } else {
        this.melting.meltNo = meltNo;
        this.productionService.saveMelting(this.melting).subscribe((data1) => {
          this.melting = data1;
        }, (error) => {
          console.log(error);
        });
      }
    }, (error) => {
      console.log('List cannot get');
    });
  }

  getCurrentKnockOut(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentKnockOut(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.knockout = new KnockOut();
            this.knockout.batchId = element.batchId.toString();
            this.knockout.partId = element.partId;
            this.knockout.meltNo = this.melt.meltNo;
            this.knockout.startTime = undefined;
            this.knockout.endTime = undefined;
            this.productionService.saveKnockOut(this.knockout).subscribe((data2) => {
              this.knockout = data2;
            }, (error) => {
              console.log(error);
            });
          }
        });
      } else {
        this.knockout = data;
        this.knockout.startTime = this.datePipe.transform(this.knockout.startTime, 'd/MM/yy, hh:mm a');
        this.knockout.endTime = this.datePipe.transform(this.knockout.endTime, 'd/MM/yy, hh:mm a');
      }
    }, (error) => {
      console.log(error);
    });
  }
  getShotBlasting(partId: String) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentShotBlasting(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.shotBlasting = new ShotBlasting();
            this.shotBlasting.batchId = element.batchId;
            this.shotBlasting.partId = element.partId;
            this.shotBlasting.meltNo = this.melt.meltNo;
            this.shotBlasting.startTime = undefined;
            this.shotBlasting.endTime = undefined;
            this.productionService.saveShotBlasting(this.shotBlasting).subscribe((data1) => {
              this.shotBlasting = data;
            }, (error) => {
              console.log(error);
            });
          }
        });
      } else {
        this.shotBlasting = data;
        this.shotBlasting.startTime = this.datePipe.transform(this.shotBlasting.startTime, 'd/MM/yy, hh:mm a');
        this.shotBlasting.endTime = this.datePipe.transform(this.shotBlasting.endTime, 'd/MM/yy, hh:mm a');
      }
    }, (error) => {
      console.log(error);
    });
  }
  getFettling(partId: String) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentFettling(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.fettling = new Fettling();
            this.fettling.batchId = element.batchId;
            this.fettling.partId = element.partId;
            this.fettling.meltNo = this.melt.meltNo;
            this.fettling.startTime = undefined;
            this.fettling.endTime = undefined;
            this.productionService.saveFettling(this.fettling).subscribe((data1) => {
              this.fettling = data1;
            }, (error) => {
              console.log(error);
            });
          }
        });
      } else {
        this.fettling = data;
        this.fettling.startTime = this.datePipe.transform(this.fettling.startTime, 'd/MM/yy, hh:mm a');
        this.fettling.endTime = this.datePipe.transform(this.fettling.endTime, 'd/MM/yy, hh:mm a');
      }
    }, (error) => {
      console.log(error);
    });
  }
  getPainting(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentPainting(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.painting = new Painting();
            this.painting.batchId = element.batchId;
            this.painting.partId = element.partId;
            this.painting.meltNo = this.melt.meltNo;
            this.painting.startTime = undefined;
            this.painting.endTime = undefined;
            this.productionService.savePainting(this.painting).subscribe((data1) => {
              this.painting = data1;
            }, (error) => {
              console.log(error);
            });
          }
        });
      } else {
        this.painting = data;
        this.painting.startTime = this.datePipe.transform(this.painting.startTime, 'd/MM/yy, hh:mm a');
        this.painting.endTime = this.datePipe.transform(this.painting.endTime, 'd/MM/yy, hh:mm a');
      }
    }, (error) => {
      console.log(error);
    });
  }
  saveMouldingDetails() {
    this.productionService.updateMoulding(this.moulding).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }
  saveMeltingDetails() {
    this.productionService.updateMelting(this.melting).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }

  saveKnockOutDetails() {
    this.productionService.updateKnockOut(this.knockout).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
      this.knockout = data;
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }
  saveShotBlastingDetails() {
    this.productionService.updateShotBlasting(this.shotBlasting).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }
  saveFettlingDetails() {
    this.productionService.updateFettling(this.fettling).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }
  savePaintingDetails() {
    this.productionService.updatePainting(this.painting).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
      this.painting = data;
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }
}
