import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { SandDisposal } from '../../sand-disposal/sand-disposal';
import { NotifierService } from 'angular-notifier';
import { ProductionMonitoringService } from '../../production-monitoring.service';
@Component({
  selector: 'app-sand-disposal-confirm',
  templateUrl: './sand-disposal-confirm.component.html',
  styleUrls: ['./sand-disposal-confirm.component.scss']
})
export class SandDisposalConfirmComponent implements OnInit {
  public sandDisposal: SandDisposal;
  private notifier: NotifierService;
  constructor(public dialogRef: MatDialogRef<SandDisposalConfirmComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, notifier: NotifierService, public productionService: ProductionMonitoringService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    console.log('data', this.data);
  }

  updateSandDisposal() {
    this.sandDisposal = this.data;
    this.productionService.updateSandDisposal(this.sandDisposal).subscribe((data) => {
      this.dialogRef.close();
      this.notifier.notify('success', 'sand disposal Updated Successfully');
    }, error => {
      console.log(error);
      this.notifier.notify('error', 'sand disposal not updated successfully');
    });
  }


  onNoClick(): void {
    this.dialogRef.close();
  }

}
