import { Component, OnInit } from '@angular/core';
import { Product } from '../../product/product';
import { Items } from '../../items/items';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { ProductionMonitoringService} from '../production-monitoring.service';
import { MatFormFieldControl, MatDivider } from '@angular/material';
import { MatDialog } from '@angular/material/dialog';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { Http } from '@angular/http';
import {Melt} from '../melt';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'app-melt-process',
  templateUrl: './melt-process.component.html',
  styleUrls: ['./melt-process.component.scss']
})
export class MeltProcessComponent implements OnInit {
  public melt: Melt;
  public ongoing: OngoingMelts;
  private notifier: NotifierService;
  maxDate =  new Date();
  public ongoings: OngoingMelts[];

  Option = [{ _id: '', meltId: '', batchId: '', meltWeight: '', customerName: '', partId: '', quantity: '', status: '' }];
  constructor( public _dialog: MatDialog, public productionService: ProductionMonitoringService,
  public http: Http, notifier: NotifierService ) {
    this.notifier = notifier; }


  ngOnInit() {
    this.melt = new Melt();
    this.ongoing = new OngoingMelts();
    this.getCurrentMelt();
  }

  getCurrentMelt() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getOnGoingMelt();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getOnGoingMelt() {
    this.productionService.getOngoingMelt(this.melt._id).subscribe((data) => {
      this.ongoings = data;
    }, (error) => {
      console.log(error);
    });
  }

}
