import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { RawMaterial } from '../raw-material-consumption/raw-material';
import { HttpClient } from '@angular/common/http';
import { Items } from '../../items/items';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Melt } from '../melt';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-complete-raw-material',
  templateUrl: './complete-raw-material.component.html',
  styleUrls: ['./complete-raw-material.component.scss']
})
export class CompleteRawMaterialComponent implements OnInit {


  public melt: Melt;
  public itemName: String;
  private notifier: NotifierService;

  public rawMaterial: RawMaterial[];
  public consumableWeight: number;
  public rawWeight: number;

  constructor(public httpClient: HttpClient, public cdr: ChangeDetectorRef,
    private productionService: ProductionMonitoringService, notifier: NotifierService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.melt = new Melt();
    this.getCurrentMeltNo();
  }



  getCurrentMeltNo() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      if (data) {
      this.melt = data;
      this.getCurrentRawMaterial();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getCurrentRawMaterial() {
    this.productionService.getCurrentRawMaterial(this.melt.meltNo).subscribe((data) => {
      console.log('raw material', data);
      if (data) {
        this.rawMaterial = data;
        this.addrawqty();
        this.addconsumableqty();
      }

    }, (error) => {
      console.log(error);
    });
  }



  addrawqty() {
    this.rawWeight = 0;
    this.rawMaterial.forEach(res => {
      if (res.itemType === 'RawMaterial') {
        
      this.rawWeight = Number(this.rawWeight) + Number(res.itemWeight);
  
      }
    });
  }

  addconsumableqty() {
    this.consumableWeight = 0;
    this.rawMaterial.forEach(res => {
      if (res.itemType === 'Consumable') {
      this.consumableWeight = Number(this.consumableWeight) + Number(res.itemWeight);
      }
    });
  }
}
