import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { SharedService } from './shared/shared.service';
import { SandDisposalComponent } from './sand-disposal/sand-disposal.component';
import { Router } from '@angular/router';
import { ProductionMonitoringService } from './production-monitoring.service';
import {ManualTimeEntryComponent} from './manual-time-entry/manual-time-entry.component';
@Component({
  selector: 'app-production-monitoring',
  templateUrl: './production-monitoring.component.html',
  styleUrls: ['./production-monitoring.component.scss']
})
export class ProductionMonitoringComponent implements OnInit {
  public meltNo;
  constructor(public _dialog: MatDialog, public productionMonitoringService: ProductionMonitoringService,
    public sharedService: SharedService, public router: Router) { }



  ngOnInit() {
    this.getCurrentBatchStatus();
  }


  ManualTimeEntry() {
    const dialogRef = this._dialog.open(ManualTimeEntryComponent, {
      data: {}
    });
    dialogRef.afterClosed().subscribe(result => {

    });
  }


  getCurrentBatchStatus() {
    this.productionMonitoringService.getCurrentBatchStatus().subscribe(data => {
      console.log('painting', data);
      if (data) {
        this.meltNo = data.meltNo;
      }
    }, error => {
      console.log(error);
    });
  }

  sandDisposal(meltNo: String) {
    const dialogRef = this._dialog.open(SandDisposalComponent, {
      data: { meltNo: meltNo }
    });
    dialogRef.afterClosed().subscribe(result => {

    });
  }
}
