import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ShotBlastingMaster } from 'src/app/foundry/master/production-master/shot-blasting-master/shot-blasting-master';
import { ProductionMasterService } from 'src/app/foundry/master/production-master/production-master.service';
import { MachineDetails } from 'src/app/foundry/master/production-master/machine-details/machine-details';
import { ShotBlasting } from '../shot-blasting';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-multiple-machine-entry',
  templateUrl: './multiple-machine-entry.component.html',
  styleUrls: ['./multiple-machine-entry.component.scss']
})
export class MultipleMachineEntryComponent implements OnInit {
  public shotBlastingMasters: ShotBlastingMaster[];
  public machineDetails: MachineDetails[];
  public shotBlasting: ShotBlasting;
  public type;
  public otHours;
  public workinghr = [];
  public consumables = [];
  public itemNamelength = [];
  public itemNamelength1 = [];
  private notifier: NotifierService;
  Option1 = [];
  Option = [{ machineId: '', consumableName: '', perKgCost: 0, weight: undefined, totalWorkingHours: 0, otHours: 0 }];
  constructor(public dialogRef: MatDialogRef<MultipleMachineEntryComponent>, notifier: NotifierService,
    @Inject(MAT_DIALOG_DATA) public data: any, public productionMasterService: ProductionMasterService
  ) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.workinghr[0] = true;
    this.itemNamelength[0] = 0;
    this.itemNamelength1[0] = 10;
    this.shotBlasting = new ShotBlasting();
    this.getMachines(this.data.type);
    if (this.data !== undefined) {
      this.Option = this.data.Option;
    }
    console.log(this.data.toHours);
    this.otHours = this.data.otHours;
  }


  getMachines(type: string) {
    this.productionMasterService.getMachineDetailsByType(type).subscribe(
      data => {
        this.machineDetails = data;
        if (this.data.type === 'ShotBlasting') {
          this.productionMasterService.getAllShotBlastingConsumable().subscribe(
            data1 => {
              this.shotBlastingMasters = data1;
            }, err => {
              console.log(err);
            });
        } else if (this.data.type === 'Fettling') {
          this.productionMasterService.getAllFettlingConsumable().subscribe(data2 => {
            this.shotBlastingMasters = data2;
          }, err => {
            console.log(err);
          });
        }
      },
      err => {
        console.log(err);
      }
    );
  }

  getConsumables(i, machineId: string) {
    this.consumables = [];
    let j = 0;
    this.shotBlastingMasters.forEach(element => {
      if (element.machineId === machineId) {
        this.consumables[j] = element;
        j++;
      }
    });
    if (this.Option1.length >= 1) {
      this.Option1.forEach(res => {
        if (res.machineId === machineId) {
          this.workinghr[i] = false;
        }
      });
    }
  }

  getPerKgCost(i, machineId: string, consumableName: string) {
    console.log('i', i);
    let j;
    j = 0;
    this.shotBlastingMasters.forEach(element => {
      if (element.consumableName === consumableName) {
        this.Option[i].perKgCost = element.perKgCost;
        if (element.replacementRate === 'Monthly') {
          this.Option[i].weight = element.replacementQuantity / 4 ;
        } else {
          this.Option[i].weight = element.replacementQuantity;
        }
      }
    });
    // if (this.Option1.length >= 1) {
    //   console.log('length', this.Option1.length);
    //   console.log('option', this.Option1);
    //   this.Option1.forEach(res => {
    //     console.log('machineId', res.machineId);
    //     console.log('consumableName', res.consumableName);
    //     console.log('machineId', machineId);
    //     console.log('consumableName', consumableName);
    //     if (res.machineId === machineId && res.consumableName === consumableName) {
    //       this.notifier.notify('error', 'Already Consumable exists!');
    //       this.Option[i].consumableName = '';
    //       this.Option[i].weight = '';
    //     }
    //     j++;
    //   });
    // }

  }

  add(i: number) {
    this.Option1 = this.Option;
    this.workinghr[i + 1] = true;
    this.consumables[i] = [];
    this.itemNamelength[i] = 10;
    this.itemNamelength1[i] = 10;
    i = i + 1;
    console.log('test', this.Option);
    this.Option.push({ machineId: '', consumableName: '', perKgCost: 0, weight: undefined, totalWorkingHours: 0, otHours: 0 });
    this.itemNamelength[i] = 0;
    this.itemNamelength1[i] = 10;
  }


  remove(i: number) {
    this.Option.splice(i, 1);
    this.itemNamelength[i] = 0;
    this.itemNamelength1[i] = 10;
  }


  onNoClick(): void {
    this.dialogRef.close();
  }

  savedetails() {
    console.log('this.option', this.Option);
    this.dialogRef.close(this.Option);
  }

}
