import { Component, OnInit, Inject } from '@angular/core';
import { Melt } from '../../melt';
import { CoreMaking } from '../core-making';
import { CoreMaster } from '../../../master/production-master/core-master/core-master';
import { CoreDetails } from '../core-details';
import { MatDialogRef, MAT_DIALOG_DATA, MAT_DIALOG_SCROLL_STRATEGY } from '@angular/material';
import { ProductionMasterService } from '../../../master/production-master/production-master.service';
@Component({
  selector: 'app-core-details',
  templateUrl: './core-details.component.html',
  styleUrls: ['./core-details.component.scss']
})
export class CoreDetailsComponent implements OnInit {
  public type;
  public moldType;
  public coreMaking: CoreMaking;
  public coreMakings: CoreMaking[];
  public coreMasters: CoreMaster[];
  public coreDetails = [];
  public itemNamelength = [];
  public itemNamelength1 = [];
  Option = [{ coreWeight: 0, coreType: '' }];
  constructor(public dialogRef: MatDialogRef<CoreDetailsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public productionMasterService: ProductionMasterService) {
  }

  ngOnInit() {
    this.moldType = this.data.moldType;
    this.type = 'CoreMaking';
    this.coreMaking = new CoreMaking();
    this.itemNamelength[0] = 0;
    this.itemNamelength1[0] = 10;
    this.getCoreTypes(this.moldType );
    console.log(this.data.Option);
    if (this.data.Option.length > 0) {
      this.Option = this.data.Option;
    }
  }

  getCoreTypes(moldType: String) {
    this.productionMasterService.getCoreType(moldType).subscribe(data => {
      this.coreMasters = data;
    }, error => {
      console.log(error);
    });
  }

  add(i: number) {
    console.log(this.Option);
      i = i + 1;
    this.Option.push({coreWeight: 0, coreType: '' });
    this.itemNamelength[i] = 0;
    this.itemNamelength1[i] = 10;
  }

  remove(i: number) {
    this.Option.splice(i, 1);
    this.itemNamelength[i] = 0;
    this.itemNamelength1[i] = 10;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  save() {
    this.dialogRef.close(this.Option);
  }

}
