import { Component, OnInit } from '@angular/core';
import { Melt } from '../melt';
import { CoreMaking } from './core-making';
import { NotifierService } from 'angular-notifier';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { TimeValidationComponent } from '../time-validation/time-validation.component';
import { MatDialog } from '@angular/material';
import { CoreMaster } from '../../master/production-master/core-master/core-master';
import { CoreDetails } from './core-details';
import {CoreDetailsComponent} from './core-details/core-details.component';
@Component({
  selector: 'app-core-making',
  templateUrl: './core-making.component.html',
  styleUrls: ['./core-making.component.scss']
})
export class CoreMakingComponent implements OnInit {
  public type;
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  public coreMaking: CoreMaking;
  public coreMakings: CoreMaking[];
  public coreMasters: CoreMaster[];
  public coreDetails = [];
  public stopTime: Date;
  public startTime: Date;
  public otHour = false;
  public totalWeight: number;
  public labourMaster: LabourMaster;
  private notifier: NotifierService;
  public itemNamelength = [];
  public itemNamelength1 = [];
  Option = [{coreWeight: 0, coreType: '' }];
  constructor(public productionService: ProductionMonitoringService, public productionMasterService: ProductionMasterService,
    notifier: NotifierService, private dialog: MatDialog) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.type = 'Core';
    this.melt = new Melt();
    this.coreMaking = new CoreMaking();
    this.getCurrentMelt();
    this.itemNamelength[0] = 0;
    this.itemNamelength1[0] = 10;
  }
  getCurrentMelt() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getOngoingMelt();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getOngoingMelt() {
    this.productionService.getOngoingMelt(this.melt._id).subscribe((data) => {
      this.ongoingMelts = data;
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        this.coreMaking.partId = this.ongoingMelts[0].partId;
      }
      this.getCoreMaking(this.coreMaking.partId);
    }, (error) => {
      console.log(error);
    });
  }

  getCoreMaking(partId: String) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentCoreMaking(batchId).subscribe((data) => {
      this.coreMakings = data;
      console.log('this.coreMakings', this.coreMakings);
      if (data.length >= 1) {
        this.coreMakings.forEach(res => {
          this.coreMaking._id = res._id;
          this.coreMaking.batchId = res.batchId;
          this.coreMaking.partId = res.partId;
          this.coreMaking.startTime = res.startTime;
          this.coreMaking.endTime = res.endTime;
          this.stopTime = this.coreMaking.endTime;
          this.coreMaking.totalWeight = res.totalWeight;
          this.startTime = this.coreMaking.startTime;
          this.coreMaking.meltNo = res.meltNo;
          this.coreMaking.moldType = res.moldType;
          this.coreMaking.totalWorkingHours = res.totalWorkingHours;
          this.coreMaking.coreDetails = res.coreDetails;
          this.Option = this.coreMaking.coreDetails;
          // if (this.coreMaking.coreDetails !== undefined) {
          //   let i = 0;
          //   this.coreMaking.coreDetails.forEach(res1 => {
          //     console.log('res.coreType', res1.coreType);
          //     this.Option[i].coreType = res1.coreType;
          //     this.Option[i].coreWeight = res1.coreWeight;
          //     i++;
          //   });
          // }
          console.log(this.coreMaking);
          this.coreMaking.otHours = res.otHours;
          this.addweight();
          this.getLabourMaster(this.coreMaking.moldType);
          this.getCoreTypes(this.coreMaking.moldType);
        });
      }
    }, (error) => {
      console.log(error);
    });
  }

  getLabourMaster(moldType: String) {
    this.type = this.type + '-' + moldType;
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(this.type).subscribe(data => {
      this.labourMaster = data;
      if (this.labourMaster.otHours) {
        this.otHour = true;
      }
    }, error => {
      console.log(error);
    });
  }

  getCoreTypes(moldType: String) {
    this.productionMasterService.getCoreType(moldType).subscribe(data => {
      this.coreMasters = data;
    }, error => {
      console.log(error);
    });
  }

  coreDetailsEntry() {
    const dialogRef = this.dialog.open(CoreDetailsComponent, {
      data: { Option : this.Option, moldType:  this.coreMaking.moldType}
    });
    dialogRef.afterClosed().subscribe(result => {

      this.Option = result;
      console.log(this.Option);
      this.addweight();
    });
  }


  addweight() {
    this.totalWeight = 0;
    this.Option.forEach(res => {
      this.totalWeight = Number(this.totalWeight) + Number(res.coreWeight);
    });
    this.coreMaking.totalWeight = this.totalWeight;
  }
  coreMakingStartDate() {
    this.coreMaking.meltNo = this.melt.meltNo;
    this.startTime = new Date();
    this.coreMaking.startTime = this.startTime;
    this.coreMaking.createdBy = localStorage.getItem('user');
    this.coreMaking.moldType = this.coreMaking.moldType;
    this.productionService.updateCoreMaking(this.coreMaking).subscribe((data) => {
      this.notifier.notify('success', 'Core Making Start Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Start Core Making');
    });
  }

  coreMakingStopDate() {
    this.stopTime = new Date();
    this.coreMaking.endTime = this.stopTime;
    this.coreMaking.modifiedBy = localStorage.getItem('user');
    this.coreMaking.moldType = this.coreMaking.moldType;
    this.productionService.updateCoreMaking(this.coreMaking).subscribe((data) => {
      this.notifier.notify('success', 'Core Making End Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not End Core Making');
    });
  }


  SaveCoreMakingDetails() {
    if (this.coreMaking.endTime !== undefined && this.coreMaking.totalWorkingHours !== undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.coreMaking.endTime)).getTime() - (new Date(this.coreMaking.startTime)).getTime();
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      let seconds;
      seconds = Math.round(timeDiff);
      let hour;
      hour = Math.round(seconds / 60);
      let minutes;
      minutes = seconds % 60;
      let total;
      total = Number(hour + '.' + minutes);
      if (this.coreMaking.otHours === undefined) {
        this.coreMaking.otHours = 0;
      }
      if (total < Number(this.coreMaking.totalWorkingHours) + Number(this.coreMaking.otHours)) {
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }
    this.Option.forEach(element => {
      this.coreDetails.push({ _id: '', batchId: this.coreMaking.batchId, coreWeight: element.coreWeight, coreType: element.coreType });
    });
    this.coreMaking.coreDetails = this.coreDetails;
    this.coreMaking.modifiedBy = localStorage.getItem('user');
    console.log(this.coreMaking);
    this.productionService.updateCoreMaking(this.coreMaking).subscribe((data) => {
      this.notifier.notify('success', 'Core Details Saved Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Saved Core Details');
    });

  }
}
