import { Component, OnInit } from '@angular/core';
import { ShotBlasting } from './shot-blasting';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { HttpClient } from '@angular/common/http';
import { Product } from '../../product/product';
import { Melt } from '../melt';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { NotifierService } from 'angular-notifier';
import { ShotBlastingMaster } from '../../master/production-master/shot-blasting-master/shot-blasting-master';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { TimeValidationComponent } from '../time-validation/time-validation.component';
import { MultipleMachineEntryComponent } from './multiple-machine-entry/multiple-machine-entry.component';
import { MachineDetails } from '../../master/production-master/machine-details/machine-details';
@Component({
  selector: 'app-shot-blasting',
  templateUrl: './shot-blasting.component.html',
  styleUrls: ['./shot-blasting.component.scss']
})
export class ShotBlastingComponent implements OnInit {
  public shotBlasting: ShotBlasting;
  public type;
  public consumables = [];
  public shots = [{ machineId: '', name: '', perKgCost: 0, weight: 0, totalWorkingHours: 0 }];
  public products: Product[];
  public start = false;
  public startTime;
  public stopTime;
  public meltNo;
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  private notifier: NotifierService;
  public shotBlastingMasters: ShotBlastingMaster[];
  public otHour = false;
  public labourMaster: LabourMaster;
  public machineDetails: MachineDetails[];
  constructor(
    public productionService: ProductionMonitoringService,
    private dialog: MatDialog,
    public httpClient: HttpClient,
    notifier: NotifierService,
    public productionMasterService: ProductionMasterService
  ) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.type = 'ShotBlasting';
    this.melt = new Melt();
    this.shotBlasting = new ShotBlasting();
    this.getCurrentMeltNo();
    this.getLabourMaster();
  }

  getCurrentMeltNo() {
    this.productionService.getCurrentBatchStatus().subscribe(
      data => {
        if (data) {
          this.melt = data;
          this.getCheckKnockOut();
        }
      },
      error => {
        console.log(error);
      }
    );
  }

  getCheckKnockOut() {
    this.productionService.getCheckKnockOut(this.melt.meltNo).subscribe(
      data => {
        this.ongoingMelts = data;
        for (let i = 0; i < this.ongoingMelts.length; i++) {
          this.shotBlasting.batchId = this.ongoingMelts[0].batchId;
          this.shotBlasting.partId = this.ongoingMelts[0].partId;
          this.shotBlasting.moldType = this.ongoingMelts[0].moldType;
          this.start = true;
        }
        this.getshotBlasting(this.shotBlasting.partId);
      },
      error => {
        console.log(error);
      }
    );
  }

  getAllShotBlastingMachine(type: string) {
    this.productionMasterService.getMachineDetailsByType(type).subscribe(
      data => {
        this.machineDetails = data;
      },
      err => {
        console.log(err);
      }
    );
  }

  getshotBlasting(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentShotBlasting(batchId).subscribe(
      data => {
        if (data == null) {
          this.ongoingMelts.forEach(element => {
            if (partId === element.partId) {
              this.shotBlasting = new ShotBlasting();
              this.shotBlasting.batchId = element.batchId;
              this.shotBlasting.partId = element.partId;
              this.shotBlasting.meltNo = this.melt.meltNo;
              this.shotBlasting.moldType = element.moldType;
              this.startTime = undefined;
              this.stopTime = undefined;
              this.getAllShotBlastingMachine(this.type);
            }
          });
        } else {
          this.shotBlasting = data;
          this.startTime = this.shotBlasting.startTime;
          this.stopTime = this.shotBlasting.endTime;
          this.getAllShotBlastingMachine(this.type);
        }
      },
      error => {
        console.log(error);
      }
    );
  }

  getLabourMaster() {
    this.productionMasterService
      .getLabourMasterDetailsUsingActivityType(this.type)
      .subscribe(
        data => {
          this.labourMaster = data;
          if (this.labourMaster.otHours) {
            this.otHour = true;
          }
        },
        error => {
          console.log(error);
        }
      );
  }

  getConsumables(machineId: string) {
    this.consumables = [];
    this.productionMasterService.getAllShotBlastingConsumable().subscribe(
      data => {
        this.shotBlastingMasters = data;
        let i = 0;
        this.shotBlastingMasters.forEach(element => {
          if (element.machineId === machineId) {
            this.consumables[i] = element;
            i++;
          }
        });
      },
      err => {
        console.log(err);
      }
    );
  }

  startTimeshotBlasting(peakTemp: number) {
    this.startTime = new Date();
    this.shotBlasting.startTime = this.startTime;
    this.shotBlasting.meltNo = this.melt.meltNo;
    this.shotBlasting.createdBy = localStorage.getItem('user');
    this.productionService.saveShotBlasting(this.shotBlasting).subscribe(
      data => {
        this.notifier.notify('success', 'Shot Blasting Start Successfully');
        this.shotBlasting = data;
      },
      error => {
        console.log(error);
        this.notifier.notify('error', 'Not Start Shot Blasting');
      }
    );
  }

  stopTimeshotBlasting() {
    this.stopTime = new Date();
    this.shotBlasting.endTime = this.stopTime;
    this.shotBlasting.meltNo = this.melt.meltNo;
    this.shotBlasting.modifiedBy = localStorage.getItem('user');
    this.productionService.updateShotBlasting(this.shotBlasting).subscribe(
      data => {
        this.notifier.notify('success', 'Shot Blasting End Successfully');
      },
      error => {
        console.log(error);
        this.notifier.notify('error', 'Not End Shot Blasting');
      }
    );
  }

  SaveShotBlastingDetails() {
    if (
      this.shotBlasting.endTime !== undefined && this.shotBlasting.totalWorkingHours !== undefined
    ) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff =
        new Date(this.shotBlasting.endTime).getTime() -
        new Date(this.shotBlasting.startTime).getTime();
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      let seconds;
      seconds = Math.round(timeDiff);
      let hour;
      hour = Math.round(seconds / 60);
      let minutes;
      minutes = seconds % 60;
      let total;
      total = Number(hour + '.' + minutes);
      if (this.shotBlasting.otHours === undefined) {
        this.shotBlasting.otHours = 0;
      }
      if (total < Number(this.shotBlasting.totalWorkingHours) + Number(this.shotBlasting.otHours)
      ) {
        const dialogRef = this.dialog.open(TimeValidationComponent, {});
        dialogRef.afterClosed().subscribe(result => { });
      }
    }
    let j = 0;
    this.consumables.forEach(element => {
      this.shots[j].machineId = this.shotBlasting.machineId;
      this.shots[j].name = element.consumableName;
      this.shots[j].weight = element.replacementQuantity;
      this.shots[j].perKgCost = element.perKgCost;
      this.shots.push({ machineId: '', name: '', perKgCost: 0, weight: 0, totalWorkingHours: 0 });
      j++;
    });
    this.shots.pop();
    this.shotBlasting.consumable = this.shots;
    this.shotBlasting.modifiedBy = localStorage.getItem('user');
    this.productionService.updateShotBlasting(this.shotBlasting).subscribe(
      data => {
        this.notifier.notify('success', 'Shot Blasting Save Successfully');
      },
      error => {
        console.log(error);
        this.notifier.notify('error', 'Not Save Shot Blasting');
      }
    );
  }

  multipleMachine() {
    console.log(this.shotBlastingMasters);
    const dialogRef = this.dialog.open(MultipleMachineEntryComponent, {
      data: { type: this.type, Option: this.shots, otHour: this.otHour }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('result', result);
      this.shots = result;
    });
  }
}
