import { Component, OnInit } from '@angular/core';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Melt } from '../melt';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { Moulding } from '../moulding/moulding';

@Component({
  selector: 'app-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: ['./progress-bar.component.scss']
})
export class ProgressBarComponent implements OnInit {
  public value: number;
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  public mouldings: Moulding[];
  ongoing1: any;
  tempMouldingValue: number;
  mouldingValue: number;
  Option = [{ batchId: '', partId: '' }];
  constructor(public productionService: ProductionMonitoringService) { }

  ngOnInit() {
    this.melt = new Melt();
    this.getCurrentMelt();
    this.value = 0;

  }


  getCurrentMelt() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      data.forEach(element => {
        this.melt._id = element._id;
        this.melt.meltNo = element.meltNo;
      });
      console.log('Moulding ', this.melt);
      this.getPercentage(this.melt._id);
    }, (error) => {
      console.log(error);
    });
  }

  getPercentage(meltId: String) {
    console.log('getPercentage');
    this.productionService.getPercentage(meltId).subscribe(data => {
      console.log('value', data);
      this.value = data;
    }, err => {
      console.log(err);
    });
  }
}
