import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {SandDisposal} from '../sand-disposal';
import {Vendor} from '../../../vendor/vendor';
import {ProductionMonitoringService} from '../../production-monitoring.service';
import {VendorService} from '../../../vendor/vendor.service';
@Component({
  selector: 'app-sand-item-disposal',
  templateUrl: './sand-item-disposal.component.html',
  styleUrls: ['./sand-item-disposal.component.scss']
})
export class SandItemDisposalComponent implements OnInit {

  public vendor: Vendor[];
  public disposal: SandDisposal;

  constructor(public dialogRef: MatDialogRef<SandItemDisposalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public productionService: ProductionMonitoringService,
    private vendorService: VendorService) { }

  ngOnInit() {
    this.disposal = new SandDisposal();
    this.disposal = this.data.disposal;
    this.getSandDisposalVendor();
  }

  onNoClick(): void {
    this.dialogRef.close();

  }

  saveSandDisposal() {
    this.disposal.createdBy = localStorage.getItem('user');
    this.productionService.saveSandDisposal(this.disposal).subscribe((data) => {
      console.log(data);
      this.dialogRef.close();
    }, (error) => {
      console.log(error);
    });
  }

  getSandDisposalVendor() {
    this.vendorService.getSandDisposalVendor().subscribe((data) => {
      console.log('vendor', data);
      this.vendor = data;
    }, (error) => {
      console.log(error);
    });
  }

}
