import { Component, OnInit } from '@angular/core';
import { Fettling } from './fettling';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Product } from '../../product/product';
import { HttpClient } from '@angular/common/http';
import { MatDialog } from '@angular/material';
import { Melt } from '../melt';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { NotifierService } from 'angular-notifier';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { FettlingMaster } from '../../master/production-master/fettling-master/fettling-master';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { TimeValidationComponent } from '../time-validation/time-validation.component';
import { MultipleMachineEntryComponent } from '../shot-blasting/multiple-machine-entry/multiple-machine-entry.component';
import { MachineDetails } from '../../master/production-master/machine-details/machine-details';
@Component({
  selector: 'app-fettling',
  templateUrl: './fettling.component.html',
  styleUrls: ['./fettling.component.scss']
})
export class FettlingComponent implements OnInit {
  public fettling: Fettling;
  public products: Product[];
  public startTime;
  public stopTime;
  public start = false;
  public type: string;
  public consumables = [];
  public shots = [{ machineId: '', name: '', perKgCost: 0, weight: 0 }];
  public machineDetails: MachineDetails[];
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  public paintingType;
  private notifier: NotifierService;
  public otHour = false;
  public labourMaster: LabourMaster;
  public fettlingMasters: FettlingMaster[];
  constructor(public productionService: ProductionMonitoringService, private dialog: MatDialog,
    public httpClient: HttpClient, notifier: NotifierService, public productionMasterService: ProductionMasterService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.type = 'Fettling';
    this.melt = new Melt();
    this.fettling = new Fettling();
    this.getCurrentMeltNo();
    this.getFettlingMachineDetails();
    this.getLabourMaster();
  }

  getCurrentMeltNo() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getCheckShotBlasing();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getFettlingMachineDetails() {
    this.productionMasterService.getAllFettlingConsumable().subscribe((data) => {
      this.fettlingMasters = data;
    }, error => {
      console.log(error);
    });
  }
  getCheckShotBlasing() {
    this.productionService.getCheckShotBlasting(this.melt.meltNo).subscribe((data) => {
      this.ongoingMelts = data;
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        this.fettling.batchId = this.ongoingMelts[0].batchId;
        this.fettling.partId = this.ongoingMelts[0].partId;
        this.fettling.moldType = this.ongoingMelts[0].moldType;
        this.start = true;
      }
      this.getfettling(this.fettling.partId);
    }, (error) => {
      console.log(error);
    });
  }

  getOngingMelt(batchId: number) {
    this.productionService.getOngoingMeltBatch(batchId).subscribe((data) => {
      console.log('data', data);
      this.paintingType = data.paintingType;
    }, (error) => {
      console.log(error);
    });
  }

  getfettling(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
        this.getOngingMelt(batchId);
      }
    });
    this.productionService.getCurrentFettling(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.fettling = new Fettling();
            this.fettling.batchId = element.batchId;
            this.fettling.partId = element.partId;
            this.fettling.meltNo = this.melt.meltNo;
            this.fettling.moldType = element.moldType;
            this.startTime = undefined;
            this.stopTime = undefined;
            this.getAllFettlingMachine(this.type);
          }
        });
      } else {
        this.fettling = data;
        this.startTime = this.fettling.startTime;
        this.stopTime = this.fettling.endTime;
        this.getAllFettlingMachine(this.type);
      }
    }, (error) => {
      console.log(error);
    });
  }

  getLabourMaster() {
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(this.type).subscribe(data => {
      this.labourMaster = data;
      if (this.labourMaster.otHours) {
        this.otHour = true;
      }
    }, error => {
      console.log(error);
    });
  }

  getAllFettlingMachine(type: string) {
    this.productionMasterService.getMachineDetailsByType(type).subscribe(
      data => {
        this.machineDetails = data;
      },
      err => {
        console.log(err);
      }
    );
  }


  getConsumables(machineId: string) {
    this.consumables = [];
    this.productionMasterService.getAllFettlingConsumable().subscribe(
      data => {
        this.fettlingMasters = data;
        let i = 0;
        this.fettlingMasters.forEach(element => {
          if (element.machineId === machineId) {
            this.consumables[i] = element;
            i++;
          }
        });
      },
      err => {
        console.log(err);
      }
    );
  }

  startTimeFettling(peakTemp: number) {
    this.startTime = new Date();
    this.fettling.startTime = this.startTime;
    this.fettling.paintingType = this.paintingType;
    this.fettling.meltNo = this.melt.meltNo;
    this.fettling.createdBy = localStorage.getItem('user');
    this.productionService.saveFettling(this.fettling).subscribe((data) => {
      this.notifier.notify('success', 'Fettling Start Successfully');
      this.fettling = data;
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Start Fettling');
    });
  }

  stopTimeFettling() {
    console.log(this.fettling);
    this.stopTime = new Date();
    this.fettling.endTime = this.stopTime;
    this.fettling.modifiedBy = localStorage.getItem('user');
    this.productionService.updateFettling(this.fettling).subscribe((data) => {
      this.notifier.notify('success', 'Fettling End Successfully');
      if (this.paintingType === 'NO') {
        this.productionService.updateStatusOnGoingMelt(this.fettling.batchId).subscribe((data1) => {
          this.notifier.notify('success', 'On Going Melt this Batch Successfully Completed');
          this.productionService.checkPainting(this.fettling.meltNo).subscribe((data2) => {
            this.notifier.notify('success', ' Melt Batch Status Successfully Completed');
          }, (error) => {
            console.log(error);
            this.notifier.notify('success', 'Melt Batch Status not Completed');
          });
        }, (error) => {
          console.log(error);
          this.notifier.notify('success', 'On Going Melt this Batch not Completed');
        });
      }
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not End Fettling');
    });
  }

  SaveFettlingDetails() {
    console.log(this.fettling);
    if (this.fettling.endTime !== undefined && this.fettling.totalWorkingHours !== undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.fettling.endTime)).getTime() - (new Date(this.fettling.startTime)).getTime();
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      let seconds;
      seconds = Math.round(timeDiff);
      let hour;
      hour = Math.round(seconds / 60);
      let minutes;
      minutes = seconds % 60;
      let total;
      total = Number(hour + '.' + minutes);
      if (this.fettling.otHours === undefined) {
        this.fettling.otHours = 0;
      }
      if (total < Number(this.fettling.totalWorkingHours) + Number(this.fettling.otHours)) {
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }
    let j = 0;
    this.consumables.forEach(element => {
      this.shots[j].machineId = this.fettling.machineId.toString();
      this.shots[j].name = element.consumableName;
      this.shots[j].weight = element.replacementQuantity;
      this.shots[j].perKgCost = element.perKgCost;
      this.shots.push({ machineId: '', name: '', perKgCost: 0, weight: 0 });
      j++;
    });
    this.shots.pop();
    this.fettling.consumable = this.shots;
    this.fettling.modifiedBy = localStorage.getItem('user');
    this.productionService.updateFettling(this.fettling).subscribe((data) => {
      this.notifier.notify('success', 'Fetting Save Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Save Fetting');
    });
  }


  multipleMachine() {
    const dialogRef = this.dialog.open(MultipleMachineEntryComponent, {
      data: { type: this.type, Option: this.shots, otHours: this.otHour }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('result', result);
      this.shots = result;
    });
  }
}
