import { Component, OnInit, ViewChild } from '@angular/core';
import { ScheduledMelts } from './scheduled-melts';
import { TempScheduledMelts } from './temp-scheduled-melt';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { MatTableDataSource, MatSort } from '@angular/material';
import { HttpClient } from '@angular/common/http';
import { Http } from '@angular/http';
import { SelectionModel } from '@angular/cdk/collections';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import {Melt} from '../melt';
@Component({
  selector: 'app-scheduled-melts',
  templateUrl: './scheduled-melts.component.html',
  styleUrls: ['./scheduled-melts.component.scss']
})
export class ScheduledMeltsComponent implements OnInit {

  public schMelts: ScheduledMelts[];
  public schMelt: ScheduledMelts;
  public tempSchMelt: TempScheduledMelts;
  index: number;
  private notifier: NotifierService;
  dataSource;
  public melt: Melt;
  selection = new SelectionModel<ScheduledMelts>(true, []);


  displayedColumns = ['actions', 'customerName', 'partId', 'moldType', 'meltWeight', 'quantity', 'partWeight',
    'totalWeight', 'deliveryDate'];
  constructor(public httpClient: HttpClient, public http: Http, private router: Router,
    public productionService: ProductionMonitoringService, notifier: NotifierService) {
      this.notifier = notifier;
      this.router.routeReuseStrategy.shouldReuseRoute = function () {
        return false;
      };
    }

  @ViewChild(MatSort) sort: MatSort;

  ngOnInit() {

    this.tempSchMelt = new TempScheduledMelts();
    this.schMelt = new ScheduledMelts();
    this.getScheduledMelts();
    this.getCurrentMeltNo();
  }
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }


  getCurrentMeltNo() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
      }
    }, (error) => {
      console.log(error);
    });
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(melts => this.selection.select(melts));
  }

  getScheduledMelts() {
    this.productionService.getScheduledMelts().subscribe((data) => {
      this.schMelts = data;
      this.dataSource = new MatTableDataSource(this.schMelts);
      this.dataSource.sort = this.sort;
    }, (error) => {
      console.log(error);
    });

  }

  tempScheduledMelts(id: String) {
    this.tempSchMelt.scheduledId = id;
    this.tempSchMelt.createdBy = localStorage.getItem('user');
    this.productionService.tempScheduledMelts(this.tempSchMelt).subscribe((data) => {
    }, (error) => {
      console.log('error');
    });
  }

  scheduleOnGoingMelt() {
    this.productionService.scheduleOnGoingMelt().subscribe((data) => {
      this.notifier.notify('success', 'Scheduled Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Scheduled');
      console.log('error');
    });
  }


}
