import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { RawMaterial } from './raw-material';
import { HttpClient } from '@angular/common/http';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Melt } from '../melt';
import { NotifierService } from 'angular-notifier';
import { MeltWeightUpdationComponent } from './melt-weight-updation/melt-weight-updation.component';
import { MatDialog} from '@angular/material';
@Component({
  selector: 'app-raw-material-consumption',
  templateUrl: './raw-material-consumption.component.html',
  styleUrls: ['./raw-material-consumption.component.scss']
})
export class RawMaterialConsumptionComponent implements OnInit {

  public melt: Melt;
  public rawMaterials: RawMaterial[];
  private notifier: NotifierService;

  public consumableWeight: number;
  public rawWeight: number;

  constructor(private productionService: ProductionMonitoringService,
    private dialog: MatDialog,notifier: NotifierService) {
    this.notifier = notifier;
  }

  ngOnInit() {
    this.melt = new Melt();
    this.getCurrentMeltNo();
  }


  getCurrentMeltNo() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getCurrentRawMaterial();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getCurrentRawMaterial() {
    this.productionService.getCurrentRawMaterial(this.melt.meltNo).subscribe((data) => {
      if (data) {
        this.rawMaterials = data;
        this.addrawqty();
        this.addconsumableqty();
      }
    }, (error) => {
      console.log(error);
    });
  }


  addrawqty() {
    this.rawWeight = 0;
    this.rawMaterials.forEach(res => {
      if (res.itemType === 'RawMaterial') {
        this.rawWeight = Number(this.rawWeight) + Number(res.itemWeight);
      }
    });
  }

  addconsumableqty() {
    this.consumableWeight = 0;
    this.rawMaterials.forEach(res => {
      if (res.itemType === 'Consumable') {
      this.consumableWeight = Number(this.consumableWeight) + Number(res.itemWeight);
      }
    });
  }

  updateRawMAterial() {
    this.productionService.updateRawMaterial(this.rawMaterials).subscribe((data) => {
      console.log('message',data.message);
      if(data.message != 'success'){
        const dialogRef = this.dialog.open(MeltWeightUpdationComponent, {
          data:{meltNo:this.melt.meltNo,id:this.melt._id}
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      this.notifier.notify('success', 'Update Successfully');
      }
      else {
        this.notifier.notify('success', 'Update Successfully');
      }
    }, (error) => {
      this.notifier.notify('error', 'Not Update');

    });
  }

}
