import { Component, OnInit, Inject } from '@angular/core';
import { SandDisposal } from './sand-disposal';
import { SharedService } from '../shared/shared.service';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { RawMaterial } from '../raw-material-consumption/raw-material';
import { SandItemDisposalComponent } from './sand-item-disposal/sand-item-disposal.component';
@Component({
  selector: 'app-sand-disposal',
  templateUrl: './sand-disposal.component.html',
  styleUrls: ['./sand-disposal.component.scss']
})
export class SandDisposalComponent implements OnInit {
  public disposal: SandDisposal;
  public meltNo;
  public weight;
  public weight1;
  public stopTime;
  public rawMaterial: RawMaterial;
  Option = [{ itemName: '' }];
  public rawMaterials: RawMaterial[];


  constructor(public dialogRef: MatDialogRef<SandDisposalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public dialog: MatDialog,
    public sharedService: SharedService,
    public productionService: ProductionMonitoringService) { }


  ngOnInit() {
    this.meltNo = this.data.meltNo;
    console.log('this.meltNo', this.meltNo);
    this.rawMaterial = new RawMaterial();
    this.disposal = new SandDisposal();
    this.getRawMaterial();
  }

  getRawMaterial() {
    this.productionService.getCurrentRawMaterial(this.meltNo).subscribe((data) => {
      console.log(data);
      let i = 0;
      this.rawMaterials = data;
      this.rawMaterials.forEach(res => {
        if (res.itemType === 'CONSUMABLE') {
          this.disposal.meltNo = res.meltNo;
          this.Option[i].itemName = res.itemName;
          this.Option.push({ itemName: '' });
          i++;
        }
      });
      this.Option.pop();
    }, (error) => {
      console.log(error);
    });
  }

  disposals() {
   // console.log('this.disposal.totalWeight',this.disposal.totalWeight)
   // console.log('this.disposal.percentage',this.disposal.percentage)
   /* this.weight = this.disposal.totalWeight * (this.disposal.percentage) / 100;
    console.log(' this.weight', this.weight);
    this.disposal.weightDisposed =  this.weight;
    const dialogRef = this.dialog.open(SandItemDisposalComponent, {
      data: { disposal: this.disposal }
    });
    dialogRef.afterClosed().subscribe(result => {

    });*/
  }

  selectQuantity(itemName) {
    this.rawMaterials.forEach(res => {
      if (res.itemName === itemName) {
        this.disposal.totalWeight = Number(res.itemWeight);
      }
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}
