import { Component, OnInit } from '@angular/core';
import { KnockOut } from './knock-out';
import { SandDisposal } from '../sand-disposal/sand-disposal';
import { SandDisposalConfirmComponent } from '../knock-out/sand-disposal-confirm/sand-disposal-confirm.component';
import { Product } from '../../product/product';
import { HttpClient } from '@angular/common/http';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Melt } from '../melt';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { MatDialog } from '@angular/material';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { TimeValidationComponent } from '../time-validation/time-validation.component';
@Component({
  selector: 'app-knock-out',
  templateUrl: './knock-out.component.html',
  styleUrls: ['./knock-out.component.scss']
})
export class KnockOutComponent implements OnInit {
  public knockout: KnockOut;
  public labourMaster: LabourMaster;
  public products: Product[];
  public sandDisposal: SandDisposal;
  public stopTime;
  public start = false;
  public startTime;
  public melt: Melt;
  public otHour = false;
  // public pouring: Pouring;
  public ongoingMelts: OngoingMelts[];
  private notifier: NotifierService;
  constructor(public productionService: ProductionMonitoringService, public router: Router,
    public httpClient: HttpClient, public dialog: MatDialog, public productionMasterService: ProductionMasterService,
    notifier: NotifierService) {
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
    this.notifier = notifier;
  }

  ngOnInit() {
    this.melt = new Melt();
    this.knockout = new KnockOut();
    this.sandDisposal = new SandDisposal();
    this.getCurrentMeltNo();
    this.getLabourMaster();
  }

  getCurrentMeltNo() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getOngoingMelt();
      }
    }, (error) => {
      console.log(error);
    });
  }

  getOngoingMelt() {
    this.productionService.getOngoingMelt(this.melt._id).subscribe((data) => {
      this.ongoingMelts = data;
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        this.knockout.batchId = this.ongoingMelts[0].batchId.toString();
        this.knockout.partId = this.ongoingMelts[0].partId;
        this.start = true;
      }
      this.getKnockOut(this.knockout.partId);
    }, (error) => {
      console.log(error);
    });
  }

  getKnockOut(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
        this.productionService.getSandDisposalByBatchId(batchId).subscribe((data1) => {
          this.sandDisposal = data1;
          console.log('this.sandDisposal', this.sandDisposal);
        }, error => {
          console.log(error);
        });
      }
    });
    this.productionService.getCurrentKnockOut(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.knockout = new KnockOut();
            this.knockout.batchId = element.batchId.toString();
            this.knockout.partId = element.partId;
            this.knockout.meltNo = this.melt.meltNo;
            this.startTime = undefined;
            this.stopTime = undefined;
          }
        });

      } else {
        this.knockout = data;
        this.startTime = this.knockout.startTime;
        this.stopTime = this.knockout.endTime;

      }
    });

  }

  getLabourMaster() {
    let type;
    type = 'KnockOut';
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(type).subscribe(data => {
      this.labourMaster = data;
      if (this.labourMaster.otHours) {
        this.otHour = true;
      }

    }, error => {
      console.log(error);
    });
  }

  startTimeKnockOut() {
    this.router.navigated = false;
    this.startTime = new Date();
    this.knockout.startTime = this.startTime;
    this.knockout.meltNo = this.melt.meltNo;
    this.knockout.createdBy = localStorage.getItem('user');
    this.productionService.saveKnockOut(this.knockout).subscribe((data2) => {
      this.notifier.notify('success', 'Knock Out Start Successfully');
      this.knockout = data2;
      this.productionService.saveSandDisposalByBatchId(this.knockout.batchId).subscribe((data1) => {
        this.sandDisposal = data1;
        console.log('test', data1);
      }, error => {
        console.log(error);
      });

    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Start Knock Out');
    });
  }


  saveKnockOutDetails() {
    console.log(this.knockout);
    if (this.knockout.endTime !== undefined && this.knockout.totalWorkingHours !== undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.knockout.endTime)).getTime() - (new Date(this.knockout.startTime)).getTime();
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      let seconds;
      seconds = Math.round(timeDiff);
      let hour;
      hour = Math.round(seconds / 60);
      let minutes;
      minutes = seconds % 60;
      let total;
      total = Number(hour + '.' + minutes);
      if (this.knockout.otHours === undefined) {
        this.knockout.otHours = 0;
      }
      console.log('this.knockout.totalWorkingHours + this.knockout.otHours', this.knockout.totalWorkingHours + this.knockout.otHours);
      if (total < Number(this.knockout.totalWorkingHours) + Number(this.knockout.otHours)) {
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }
    this.knockout.modifiedBy = localStorage.getItem('user');
    this.productionService.updateKnockOut(this.knockout).subscribe((data) => {
      this.notifier.notify('success', 'Knock Out Save Successfully');
      this.knockout = data;
      const dialogRef = this.dialog.open(SandDisposalConfirmComponent, {
        data: { sandDisposal: this.sandDisposal }
      });
      dialogRef.afterClosed().subscribe(result => {

      });
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Save Knock Out');
    });
  }

  stopTimeKnockOut() {
    this.router.navigated = false;
    this.stopTime = new Date();
    this.knockout.endTime = this.stopTime;
    this.knockout.modifiedBy = localStorage.getItem('user');
    this.productionService.updateKnockOut(this.knockout).subscribe((data) => {
      this.notifier.notify('success', 'Knock Out End Successfully');
      this.knockout = data;
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not End Knock Out');
    });
  }
}
