import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductionMonitoringComponent } from './production-monitoring.component';
import { ManualTimeEntryComponent } from './manual-time-entry/manual-time-entry.component';

const routes: Routes = [
  {
    path: '',
    component: ProductionMonitoringComponent,
  },
  {
    path: 'manualTimeEntry',
    component: ManualTimeEntryComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductionMonitoringRoutingModule { }
