import { Component, OnInit } from '@angular/core';
import { Moulding } from './moulding';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { Melt } from '../melt';
import { NotifierService } from 'angular-notifier';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { TimeValidationComponent } from '../time-validation/time-validation.component';

@Component({
  selector: 'app-moulding',
  templateUrl: './moulding.component.html',
  styleUrls: ['./moulding.component.scss']
})
export class MouldingComponent implements OnInit {
  public moulding: Moulding;
  public melt: Melt;
  public mouldings: Moulding[];
  public stopDate: Date;
  public startDate: Date;
  public ongoingMelts: OngoingMelts[];
  private notifier: NotifierService;
  public otHour = false;
  public labourMaster: LabourMaster;
  constructor(public productionService: ProductionMonitoringService, public productionMasterService: ProductionMasterService,
    notifier: NotifierService,private dialog: MatDialog) {
    this.notifier = notifier;
  }


  ngOnInit() {
    this.melt = new Melt();
    this.moulding = new Moulding();
    this.getCurrentMelt();
  }


  getCurrentMelt() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getOngoingMelt();
      }

    }, (error) => {
      console.log(error);
    });
  }

  getOngoingMelt() {
    this.productionService.getOngoingMelt(this.melt._id).subscribe((data) => {
      console.log('getOngoingMelt', data);
      this.ongoingMelts = data;
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        this.moulding.partId = this.ongoingMelts[0].partId;
      }
      this.getMoulding(this.moulding.partId);
    }, (error) => {
      console.log(error);
    });
  }

  mouldingStartDate() {
    this.moulding.meltNo = this.melt.meltNo;
    this.startDate = new Date();
    this.moulding.startDate = this.startDate;
    this.moulding.createdBy = localStorage.getItem('user');
    this.moulding.moldType = this.moulding.moldType;
    this.productionService.updateMoulding(this.moulding).subscribe((data) => {
      this.notifier.notify('success', 'Mould Start Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Start Mould');
    });
  }

  mouldingStopDate() {
    this.stopDate = new Date();
    this.moulding.endDate = this.stopDate;
    this.moulding.modifiedBy = localStorage.getItem('user');
    this.moulding.moldType = this.moulding.moldType;

    this.productionService.updateMoulding(this.moulding).subscribe((data) => {
      this.notifier.notify('success', 'Mould End Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not End Mould');
    });
  }

  SaveMouldingDetails() {
    if (this.moulding.endDate != undefined && this.moulding.totalWorkingHours != undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.moulding.endDate)).getTime() - (new Date(this.moulding.startDate)).getTime();//in ms
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      var seconds = Math.round(timeDiff);
      var hour = Math.round(seconds/60);
      var minutes = seconds % 60;
      var total= Number(hour + '.' + minutes);
   if(this.moulding.otHours == undefined){
     this.moulding.otHours =0;
   }
      if(total < Number(this.moulding.totalWorkingHours) + Number(this.moulding.otHours)){
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }
    if(this.moulding.bottomWeight != undefined && this.moulding.topWeight != undefined){
this.moulding.weight= Number(this.moulding.topWeight) + Number(this.moulding.bottomWeight);
    }
this.moulding.modifiedBy = localStorage.getItem('user');
    this.moulding.moldType = this.moulding.moldType;
    this.productionService.updateMoulding(this.moulding).subscribe((data) => {
      this.notifier.notify('success', 'Mould Save Successfully');
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Save Mould');
    });
  }

  getMoulding(partId: String) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentMoulding(batchId).subscribe((data) => {
      this.mouldings = data;
      if (data.length >= 1) {
        this.mouldings.forEach(res => {
          this.moulding._id = res._id;
          this.moulding.batchId = res.batchId;
          this.moulding.noOfPieces = res.noOfPieces;
          this.moulding.partId = res.partId;
          this.moulding.startDate = res.startDate;
          this.moulding.endDate = res.endDate;
          this.moulding.meltNo = res.meltNo;
          this.moulding.moldType = res.moldType;
          this.moulding.totalWorkingHours = res.totalWorkingHours;
          this.moulding.otHours = res.otHours;
          this.moulding.topWeight = res.topWeight;
          this.moulding.bottomWeight = res.bottomWeight;
          this.moulding.weight = res.weight;
          this.stopDate = this.moulding.endDate;
          this.startDate = this.moulding.startDate;
          this.getLabourMaster(this.moulding.moldType);
        });
      } else {
        this.ongoingMelts.forEach(res => {
          if (partId === res.partId) {
            this.moulding = new Moulding();
            this.moulding.partId = res.partId;
            this.moulding.batchId = res.batchId;
            this.moulding.noOfPieces = res.quantity;
            this.startDate = undefined;
            this.stopDate = undefined;
            this.getLabourMaster(this.moulding.moldType);
          }
        });
      }
    }, (error) => {
      console.log(error);
    });
  }


  getLabourMaster(moldType: string) {
    let type = "Moulding-" + moldType;
    console.log('type', type)
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(type).subscribe(data => {
      this.labourMaster = data;
      console.log('this.labourMaster', this.labourMaster);
        if (  this.labourMaster.otHours) {
          this.otHour = true;
        }

    }, error => {
      console.log(error);
    })
  }


}
