import { Component, OnInit} from '@angular/core';
import { CompletedMelts } from './completed-melts';
import { ProductionMonitoringService } from '../production-monitoring.service';

@Component({
  selector: 'app-completed-melts',
  templateUrl: './completed-melts.component.html',
  styleUrls: ['./completed-melts.component.scss']
})
export class CompletedMeltsComponent implements OnInit {

  public comMelts: CompletedMelts[];
  constructor( public productionService: ProductionMonitoringService) { }

  ngOnInit() {
    this.getCompletedMelts();
  }

  getCompletedMelts() {
    this.productionService.getCompletedMelts().subscribe((data) => {
      this.comMelts = data;
    }, (error) => {
      console.log(error);
    });
  }






}
