import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Moulding } from '../../moulding/moulding';
import { ProductionMonitoringService } from '../../production-monitoring.service';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-check-moulding',
  templateUrl: './check-moulding.component.html',
  styleUrls: ['./check-moulding.component.scss'],
  providers: [DatePipe]
})
export class CheckMouldingComponent implements OnInit {

  public moulding: Moulding;
  public stopDate: Date;
  public startDate;
  constructor(public dialogRef: MatDialogRef<CheckMouldingComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private productionService: ProductionMonitoringService,
    private date: DatePipe) { }

  ngOnInit() {
    this.moulding = new Moulding();
    this.moulding = this.data.moulding;
    if (this.moulding.startDate == null) {
      this.startDate = 1;
    }
  }

  updateMelting() {
    if (this.startDate !== 1) {
      this.stopDate = new Date();
      this.moulding.endDate = this.stopDate;
    }
    if (this.startDate === 1) {
      this.moulding.endDate = new Date();
      this.moulding.startDate = new Date();
    }
    this.productionService.updateMoulding(this.moulding).subscribe((data) => {
      console.log(data);
      this.moulding = data;
      this.dialogRef.close(this.moulding.endDate);
    }, (error) => {
      console.log(error);
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}
