import { Component, OnInit } from '@angular/core';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { MatDialog } from '@angular/material/dialog';
import { OngoingMelts } from './ongoing-melts';
import { Http } from '@angular/http';
import { Melt } from '../melt';
import { NotifierService } from 'angular-notifier';
import { MasterService } from '../../master/master.service';
import { Master } from '../../master/master';
@Component({
  selector: 'app-ongoing-melts',
  templateUrl: './ongoing-melts.component.html',
  styleUrls: ['./ongoing-melts.component.scss'],

})

export class OngoingMeltsComponent implements OnInit {
  public melt: Melt;
  public ongoing: OngoingMelts[];
  public master: Master[];
  private notifier: NotifierService;
  maxDate = new Date();
  public melts;
  constructor(public _dialog: MatDialog, public productionService: ProductionMonitoringService,
    public http: Http, notifier: NotifierService, private masterService: MasterService) {
    this.notifier = notifier;
  }


  ngOnInit() {
    this.melt = new Melt();
    this.getCurrentMelt();
    this.getMoldType();
  }

  getCurrentMelt() {
    this.productionService.getCurrentMeltStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getOngoingMelt();
        this.melts = false;
      } else {
        this.melts = true;
      }
    }, (error) => {
      console.log(error);
    });
  }

  getOngoingMelt() {
    this.productionService.getOngoingMelt(this.melt._id).subscribe((data1) => {
      this.ongoing = data1;
      if (this.melt.totalMeltWeight === undefined) {
        this.melt.totalMeltWeight = 0;
        this.ongoing.forEach(res => {
          this.melt.totalMeltWeight = this.melt.totalMeltWeight + res.meltWeight;
        });
      } else {
        this.melt.totalMeltWeight = this.melt.totalMeltWeight;
      }
    }, (error) => {
      console.log(error);
    });
  }

  getMoldType() {
    this.masterService.getMoldingMaster().subscribe((data) => {
      this.master = data;
    }, (error) => {
      console.log(error);
    });
  }


  updateOngoingMelt() {
    this.productionService.updateCurrentMelt(this.melt).subscribe((data) => {
      this.notifier.notify('success', 'Updated  Successfully');
    }, (error) => {
      this.notifier.notify('error', 'Not Updated');
      console.log(error);
    });
  }

}
