import { Component, OnInit } from '@angular/core';
import { Painting } from './painting';
import { ProductionMonitoringService } from '../production-monitoring.service';
import { Product } from '../../product/product';
import { MatDialog} from '@angular/material';
import { Melt } from '../melt';
import { OngoingMelts } from '../ongoing-melts/ongoing-melts';
import { NotifierService } from 'angular-notifier';
import { PaintingMaster } from '../../master/production-master/painting-master/painting-master';
import { ProductionMasterService } from '../../master/production-master/production-master.service';
import { LabourMaster } from '../../master/production-master/labour-master/labour-master';
import { TimeValidationComponent } from '../time-validation/time-validation.component';
@Component({
  selector: 'app-painting',
  templateUrl: './painting.component.html',
  styleUrls: ['./painting.component.scss']
})

export class PaintingComponent implements OnInit {
  public painting: Painting;
  public products: Product[];
  public startTime;
  public stopTime;
  public start = false;
  public melt: Melt;
  public ongoingMelts: OngoingMelts[];
  private notifier: NotifierService;
  public paintingMasters: PaintingMaster[];
  public otHour = false;
  public labourMaster: LabourMaster;
  constructor(private dialog: MatDialog, public productionService: ProductionMonitoringService, public productionMasterService: ProductionMasterService,
    notifier: NotifierService) {
    this.notifier = notifier;
  }
  ngOnInit() {
    this.melt = new Melt();
    this.painting = new Painting();
    this.getCurrentMeltNo();
    this.getPaintingMachineDetails();
    this.getLabourMaster();
  }

  getCurrentMeltNo() {
    this.productionService.getCurrentBatchStatus().subscribe((data) => {
      if (data) {
        this.melt = data;
        this.getCheckKnockOut();
      }

    }, (error) => {
      console.log(error);
    });
  }

  getPaintingMachineDetails() {
    this.productionMasterService.getAllPainting().subscribe((data) => {
      this.paintingMasters = data;
    }, error => {
      console.log(error);
    });
  }

  getCheckKnockOut() {
    this.productionService.getCheckFettling(this.melt.meltNo).subscribe((data) => {
      this.ongoingMelts = data;
      console.log('test', data);
      for (let i = 0; i < this.ongoingMelts.length; i++) {
        if (this.ongoingMelts[0].paintingType !== 'NO') {
          this.painting.batchId = this.ongoingMelts[0].batchId;
          this.painting.partId = this.ongoingMelts[0].partId;
          this.start = true;
        }
      }

      this.getPainting(this.painting.partId);
    }, (error) => {
      console.log(error);
    });
  }

  getPainting(partId) {
    let batchId;
    this.ongoingMelts.forEach(element => {
      if (partId === element.partId) {
        batchId = element.batchId;
      }
    });
    this.productionService.getCurrentPainting(batchId).subscribe((data) => {
      if (data == null) {
        this.ongoingMelts.forEach(element => {
          if (partId === element.partId) {
            this.painting = new Painting();
            this.painting.batchId = element.batchId;
            this.painting.partId = element.partId;
            this.painting.meltNo = this.melt.meltNo;
            this.startTime = undefined;
            this.stopTime = undefined;
          }
        });

      } else {
        this.painting = data;
        this.startTime = this.painting.startTime;
        this.stopTime = this.painting.endTime;
      }
    }, (error) => {
      console.log(error);
    });
  }
  getLabourMaster() {
    let type = "Painting";
    this.productionMasterService.getLabourMasterDetailsUsingActivityType(type).subscribe(data => {
      this.labourMaster = data;
        if ( this.labourMaster.otHours) {
          this.otHour = true;
        }
    }, error => {
      console.log(error);
    })
  }

  startTimePainting(peakTemp: number) {
    this.startTime = new Date();
    this.painting.startTime = this.startTime;
    this.painting.meltNo = this.melt.meltNo;
    this.painting.createdBy = localStorage.getItem('user');
    this.productionService.savePainting(this.painting).subscribe((data) => {
      this.notifier.notify('success', 'Painting Start Successfully');
      this.painting = data;
    }, (error) => {
      this.notifier.notify('error', 'Not Start Painting');
      console.log(error);
    });
  }

  stopTimePainting() {
    this.stopTime = new Date();
    this.painting.endTime = this.stopTime;
    this.painting.modifiedBy = localStorage.getItem('user');
    this.productionService.updatePainting(this.painting).subscribe((data) => {
      this.notifier.notify('success', 'Painting End Successfully');
      this.painting = data;
      this.productionService.updateStatusOnGoingMelt(this.painting.batchId).subscribe((data1) => {
        this.notifier.notify('success', 'On Going Melt this Batch Successfully Completed');
        this.productionService.checkPainting(this.painting.meltNo).subscribe((data2) => {
          this.notifier.notify('success', ' Melt Batch Status Successfully Completed');
        }, (error) => {
          console.log(error);
          this.notifier.notify('success', 'Melt Batch Status not Completed');
        });
      }, (error) => {
        console.log(error);
        this.notifier.notify('success', 'On Going Melt this Batch not Completed');
      });
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'On Going Melt Not Completed');
    });
  }

  SavePaintingDetails() {
    if (this.painting.endTime != undefined && this.painting.totalWorkingHours != undefined) {
      /* link :https://stackoverflow.com/questions/41632942/how-to-measure-time-elapsed-on-javascript  */
      let timeDiff = (new Date(this.painting.endTime)).getTime() - (new Date(this.painting.startTime)).getTime();//in ms
      // strip the ms
      timeDiff /= 60000;
      // get seconds
      var seconds = Math.round(timeDiff);
      var hour = Math.round(seconds/60);
      var minutes = seconds % 60;
      var total= Number(hour + '.' + minutes);
      if(this.painting.otHours == undefined){
        this.painting.otHours =0;
      }
      if(total < Number(this.painting.totalWorkingHours) +Number(this.painting.otHours)){
        const dialogRef = this.dialog.open(TimeValidationComponent, {
        });
        dialogRef.afterClosed().subscribe(result => {
        });
      }
    }

    this.painting.modifiedBy = localStorage.getItem('user');
    this.productionService.updatePainting(this.painting).subscribe((data) => {
      this.notifier.notify('success', 'Painting Save Successfully');
      this.painting = data;
    }, (error) => {
      console.log(error);
      this.notifier.notify('error', 'Not Start Painting');
    });
  }
}
